import Router from 'vue-router'
import Vue from 'vue'
import blogRoute from './blog.js'
// import vueModelRoutes from './vue.js' // vue模块路由
// import personPageRoutes from './personPage.js' // 个人网站路由模块
Vue.use(Router)
const routes = [
  ...blogRoute
  /* ...personPageRoutes,
  ...vueModelRoutes */
]
const router = new Router({ 
  mode: 'history',
  routes
})
// 路由守卫设置
router.beforeEach((to, form, next) => {
  window.document.title = to?.meta?.title || ''
  next()
})
export default router