import {
  Button,
  Select,
  Input,
  Message,
  MessageBox,
  Notification,
  Tabs,
  TabPane,
  Tooltip
} from 'element-ui'
const element = {
  install: function(Vue) {
    Vue.use(Button)
    Vue.use(Select)
    Vue.use(Input)
    Vue.use(Tabs)
    Vue.use(TabPane)
    Vue.use(Tooltip)

    Vue.prototype.$message = Message
    Vue.prototype.$confirm = MessageBox.confirm
    Vue.prototype.$alert = MessageBox.alert
    Vue.prototype.$notify = Notification
  }
}
export default element

