import VueBaseIndex from './vue2/index.vue'
import TsBaseIndex from './typeScript/index.vue'
import ReactBaseIndex from './react/index.vue'
import RnBaseIndex from './reactNative/index.vue'
import EsSkillBaseIndex from './esSkill/index.vue'
import BrowerBaseIndex from './brower/index.vue'
import JsSkillBaseIndex from './jsSkill/index.vue'
import fontPerBaseIndex from './frontPerformance/index.vue'
import WebCss from './webCss/index.vue'

export default {
  path: '/webFront',
  name: 'webFront',
  meta: { title: '前端' },
  children: [
    {
      path: '/webFront/webCss',
      name: 'webCss',
      meta: { title: '前端小知识' },
      component: WebCss
    },
    {
      path: '/webFront/vue2',
      name: 'vue2',
      meta: { title: 'vue2' },
      component: VueBaseIndex
    },
    {
      path: '/webFront/ts',
      name: 'typeScript',
      meta: { title: 'typeScript' },
      component: TsBaseIndex
    },
    {
      path: '/webFront/react',
      name: 'react',
      meta: { title: 'react' },
      component: ReactBaseIndex
    },
    {
      path: '/webFront/rn',
      name: 'reactNative',
      meta: { title: 'reactNative' },
      component: RnBaseIndex
    },
    {
      path: '/webFront/esSkill',
      name: 'esSkill',
      meta: { title: 'ES小技巧' },
      component: EsSkillBaseIndex
    },
    {
      path: '/webFront/brower',
      name: 'brower',
      meta: { title: '浏览器知识' },
      component: BrowerBaseIndex
    },
    {
      path: '/webFront/jsSkill',
      name: 'jsSkill',
      meta: { title: 'JS小技巧' },
      component: JsSkillBaseIndex
    },
    {
      path: '/webFront/nature',
      name: 'nature',
      meta: { title: '前端性能' },
      component: fontPerBaseIndex
    }
  ]
}